<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="所有消费类型" style="width:240px;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder="开始时间" style="width:240px;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder="结束时间" style="width:240px;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder="卡号/手机号" style="width:300px;"></el-input>
					<el-button type="info" plain v-on:click="getUsers" class="searchkey">搜索</el-button>
				</el-form-item>
	
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="users" stripe highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="index">
			</el-table-column>
			<el-table-column type="selection">
			</el-table-column>
			<el-table-column prop="name" label="会员名称">
			</el-table-column>
			<el-table-column prop="sex" label="卡号" :formatter="formatSex">
			</el-table-column>
			<el-table-column prop="age" label="联系电话">
			</el-table-column>
			<el-table-column prop="age" label="会员级别">
			</el-table-column>
			<el-table-column prop="age" label="消费累积金额">
			</el-table-column>
			<el-table-column prop="age" label="余额">
			</el-table-column>
			<el-table-column prop="age" label="充值场馆">
			</el-table-column>
			<el-table-column prop="age" label="状态">
			</el-table-column>
			<el-table-column label="操作">
				<template scope="scope">
					<el-button class="tablebtn" size="small" @click="handledetail(scope.$index, scope.row)">详情</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		
		<!--消费明细-->
		<el-dialog title="详情" :visible.sync="limitedDeta">
				
		</el-dialog>
			
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				limitedDeta: false,//详情是否显示
				
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//新增界面数据
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		methods: {
			//性别显示转换
			formatSex: function (row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//显示详情界面		
			handledetail(index, row) {
				this.limitedDeta = true;
				console.log(index, row);
				},		
			selsChange: function (sels) {
				this.sels = sels;
			},
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style>

</style>


