			<el-table-column prop="birth" label="有效日期" sortable>
			</el-table-column>
			<el-table-column prop="birth" label="营业场馆">
			</el-table-column>
			<el-table-column prop="birth" label="来源">
			</el-table-column>
			<el-table-column label="操作" width="200">
				<template scope="scope">
					<el-button class="tablebtn" size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<span style="color: #7692cc;">|</span>
					<el-button class="tablebtn" size="small" @click="handleRole(scope.$index, scope.row)">充值</el-button>
					<span style="color: #7692cc;">|</span>
					<el-button class="tablebtn" size="small" @click="handledetail(scope.$index, scope.row)">消费明细</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		
		<!--消费明细-->
		<el-dialog title="消费明细" :visible.sync="limitedDeta">
					 <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
							<el-form :inline="true" :model="filters">	
								<el-form-item>
									<el-input v-model="filters.name" placeholder="开始时间" style="width:230px;"></el-input>
								</el-form-item>
								<el-form-item>
									<el-input v-model="filters.name" placeholder="结束时间" style="width:230px;"></el-input>
								</el-form-item>
								<el-form-item>
									<el-button type="info" plain v-on:click="getUsers" class="searchkey">搜索</el-button>
								</el-form-item>
					
							</el-form>
						</el-col>
				
						<!--列表-->
						<el-table :data="users" stripe highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
							<el-table-column type="index" width="60">
							</el-table-column>
							<el-table-column prop="name" label="姓名">
							</el-table-column>
							<el-table-column prop="sex" label="卡号" :formatter="formatSex">
							</el-table-column>
							<el-table-column prop="age" label="会员类型">
							</el-table-column>
							<el-table-column prop="birth" label="开始时间">
							</el-table-column>
							<el-table-column prop="birth" label="结束时间">
							</el-table-column>
							<el-table-column prop="birth" label="消费金额">
							</el-table-column>
							<el-table-column prop="birth" label="余额">
							</el-table-column>
							<el-table-column prop="birth" label="消费场馆">
							</el-table-column>
							<el-table-column prop="birth" label="行为">
							</el-table-column>
						</el-table>
				
		</el-dialog>
		
		<!--编辑界面-->
		<el-dialog title="编辑会员" :visible.sync="limitedEdit">
			<el-form :model="editForm" label-width="110px" :rules="editFormRules" ref="editForm" class="addform">
				<el-form-item label="营业场馆" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="会员级别" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="会员姓名">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="性别">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="手机">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="公司">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="卡号">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="销售">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="来源">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
			</el-form>
			<div slot="footer" class="dialog-footer" style="width: 95px;margin: 0px auto;">
				<el-button class="rear_bg" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--会员充值界面-->
		<el-dialog title="会员充值" :visible.sync="limitedRole">
			<el-form :model="editForm" label-width="110px" :rules="editFormRules" ref="editForm" class="addform" style="width: 80%;margin: 0px auto;">
				<el-row>
					<el-col :span="12">
						<div class="grid-content">
							<el-form-item label="营业场馆" prop="name">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>
							
							<el-form-item label="会员级别" prop="name">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>
							
							<el-form-item label="公司">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>							
						</div>
					</el-col>
					<el-col :span="12">
						<div class="grid-content">
							<el-form-item label="会员姓名" prop="name">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>
							
							<el-form-item label="会员手机" prop="name">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>
							
							<el-form-item label="会员卡号">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>
						</div>
					</el-col>
				</el-row>
				<el-row>
					<el-col :span="24" style="height:2px;background: #f2f2f2;margin-bottom: 20px;">
					</el-col>
				</el-row>		
				<el-row>
					<el-col :span="12">
						<div class="grid-content">
							<el-form-item label="当前余额" prop="name">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>
							
							<el-form-item label="充值金额" prop="name">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>
							
							<el-form-item label="充值方式">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>	
							<el-form-item label="销售">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>		
							<el-form-item label="备注">
								<el-input v-model="addForm.name" auto-complete="off"></el-input>
							</el-form-item>								
						</div>
					</el-col>
					<el-col :span="12">
						<div class="grid-content">
							<el-form-item label="实际充值金额">
								<span style="font-size:24px;color:#ff6b2c;font-weight: bold;">120</span>
							</el-form-item>
						</div>
					</el-col>
				</el-row>
			</el-form>
			<div slot="footer" class="dialog-footer" style="width: 95px;margin: 0px auto;">
				<el-button class="rear_bg" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
		<!--新增界面-->
		<el-dialog title="添加会员" :visible.sync="limitedAdd">
			<el-form :model="addForm" label-width="110px" :rules="addFormRules" ref="addForm" class="addform">
				<el-form-item label="营业场馆" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="会员级别" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="会员姓名">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				
				<el-form-item label="性别">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="手机">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="公司">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="卡号">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="销售">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="来源">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer" style="width: 95px;margin: 0px auto;">
				<el-button class="rear_bg" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
					id: 0,
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				},

				limitedAdd: false,//添加界面是否显示
				limitedEdit: false,//添加界面是否显示
				limitedRole: false,//添加界面是否显示
				limitedDeta: false,//消费详情是否显示
				
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//新增界面数据
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		methods: {
			//性别显示转换
			formatSex: function (row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit(index, row) {
				this.limitedEdit = true;
				console.log(index, row);
			  },
			//显示权限管理界面
			handleRole(index, row) {
				this.limitedRole = true;
				console.log(index, row);
				},
			//显示消费详情界面		
			handledetail(index, row) {
				this.limitedDeta = true;
				console.log(index, row);
				},		
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style>

</style>


